-table-column align=\"center\"\r\n                         label=\"申请时间\"\r\n                         prop=\"create_time\"></el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"操作\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"primary\"\r\n                       size=\"small\"\r\n                       :loading=\"throughLoading[scope.$index]\"\r\n                       @click=\"through(scope.row,scope.$index)\">通过</el-button>\r\n            <el-button type=\"danger\"\r\n                       size=\"small\"\r\n                       :loading=\"rejectLoading[scope.$index]\"\r\n                       @click=\"reject(scope.row,scope.$index)\">驳回</el-button>\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n\r\n      <div class=\"page_center\"\r\n           v-show=\"pageCount&&pageCount>1\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\n//  import page from '../common.js';\r\nimport Viewer from '@/utils/Viewer'\r\nexport default {\r\n  data () {\r\n    return {\r\n      authList: [],\r\n      threeAuthList: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      seCurId: 0,\r\n      page: 0,\r\n      pageCount: 0,\r\n      list: [],\r\n      detailsVisible: false,\r\n      detailsList: {},\r\n      throughLoading: [], //通过loading\r\n      rejectLoading: [],//驳回loading\r\n    }\r\n  },\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n    async init () {\r\n      try {\r\n        const res = await this.$axios.post('admin/UserAudit/serviceCode', { token: this.token, page: this.page, status: 0 })\r\n        this.list = res.data.data.list || []\r\n        this.pageCount = res.data.data.rows || 0;\r\n      } catch (err) {\r\n        throw (err)\r\n      }\r\n    },\r\n    through (item, index) {  //通过\r\n      this.$confirm('此操作通过审核, 是否继续?', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'warning'\r\n      }).then(async () => {\r\n        try {\r\n          this.throughLoading[index] = true\r\n          const res = await this.$axios.post('admin/UserAudit/serviceAudit', { token: this.token, uid: item.uid }) //uid 用户id\r\n          this.throughLoading[index] = false\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: 'success' })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (error) {\r\n          this.throughLoading[index] = false\r\n          throw (error)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n    reject (item, index) {  //驳回\r\n      this.$prompt('请输入驳回理由', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        inputPattern: /\\S/,\r\n        inputErrorMessage: '驳回理由不能为空'\r\n      }).then(async ({ value }) => {\r\n        try {\r\n          this.rejectLoading[index] = true\r\n          const res = await this.$axios.post('admin/UserAudit/serviceReject', { token: this.token, uid: item.uid, reason: value })  //uid 用户id  reason-驳回理由\r\n          this.rejectLoading[index] = false\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: 'success' })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (error) {\r\n          this.rejectLoading[index] = false\r\n          throw (error)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n    erAuth () { //权限列表\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: this.token,\r\n        id: id\r\n      })\r\n        .then(res => {\r\n\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n\r\n              if (arr[i].name == '服务工号') {\r\n                this.seCurId = arr[i].id;\r\n\r\n                if (arr[i].son) {\r\n\r\n                  this.threeAuthList = arr[i].son;\r\n                  for (var j = 0; j < arr[i].son.length; j++) {\r\n                    if (arr[i].action != arr[i].son[j].action) {\r\n                      arr[i].action = arr[i].son[0].action;\r\n                    }\r\n                    if (arr[i].son[j].name == '未审核' && arr[i].name == '服务工号') {\r\n                      this.thCurId = arr[i].son[j].id;\r\n                    }\r\n\r\n                  }\r\n                }\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n\r\n  mounted () {\r\n    this.init()\r\n    this.erAuth()\r\n  },\r\n\r\n}\r\n</script>\r\n<style scoped>\r\n.car_img {\r\n  width: 50px;\r\n  height: 50px;\r\n  vertical-align: middle;\r\n  margin: 10px 0;\r\n}\r\n.details > div {\r\n  margin: 10px 0;\r\n}\r\n.details span:first-child {\r\n  width: 100px;\r\n  text-align: right;\r\n  display: inline-block;\r\n}\r\n.img {\r\n  width: 50px;\r\n  height: 50px;\r\n  vertical-align: middle;\r\n  margin: 10px 0;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

